@import '/src/css/bootstrap.css';
.rs_underline {
  background: rgba(255, 255, 0, 0.315);
  text-decoration: underline;
}

.rs_dash {
  position: relative;
  margin-left: 40px;
}
.rs_dash:before {
  position: absolute;
  content: "----";
  left: -30px;
  color: #000;
}

.text-upperCase {
  text-transform: uppercase !important;
}

html {
  font-family: "Inter-Regular", Arial, Helvetica, sans-serif;
  font-size: medium;
  line-height: normal;
}
html p,
html a {
  font-family: "Inter-Regular", Arial, Helvetica, sans-serif;
}
html :where(.editor-styles-wrapper) {
  font-family: "Inter-Regular", Arial, Helvetica, sans-serif;
  font-size: medium;
  line-height: normal;
  color: initial;
}

body {
  font-family: "Inter-Regular", Arial, Helvetica, sans-serif;
  font-size: medium;
  line-height: normal;
}

b,
strong {
  font-family: "Inter-Regular", Arial, Helvetica, sans-serif;
}

.acf-block-preview {
  min-height: 100px !important;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  border: 1px solid gray;
}

.align-center {
  align-self: center !important;
}
.align-center .wp-block-media-text__content {
  align-self: center !important;
}